<script>
import MenuPluris from './MenuPluris.vue';

export default {
  name: 'CardPluris',
  data: () => ({
    title: '',
    content: 'content.items1[0].text',
    idPluris: -1,
    idIndex: -1,
  }),
  components: {
    MenuPluris,
  },
  methods: {
    callPageCard({ id, index }) {
      let pageTitle;
      this.idPluris = id;
      this.idIndex = index;

      if (id <= 3) {
        pageTitle = `menu.items${id}[${index}].title`;
      } else {
        pageTitle = `menu.item${id}`;
      }

      const pageContent = `content.items${id}[${index}].text`;

      this.title = this.$i18n.t(pageTitle);
      this.content = this.$i18n.t(pageContent);
    },
    callInitialPage() {
      this.$router.push({ name: 'home' });
    },
    changePageInside(id, index) {
      this.callPageCard({ id, index });
    },
  },
  created() {
    this.title = this.$i18n.t('menu.items1[0].title');
  },
};
</script>

